<template>
  <div class="row">
    <h4>Итого к выплате: {{ total }}</h4>
    <hr>
    <table>
      <thead>
        <tr>
          <th>Приход</th>
          <th>Дней</th>
          <th>Часов</th>
          <th>Сумма</th>
        </tr>
      </thead>

      <tbody>
        <tr>
          <td>Оплата по тарифу</td>
          <td>{{shiftsTotal}}</td>
          <td>{{shiftsTotal * 11}}</td>
          <td>{{tarif}}</td>
        </tr>
        <tr>
          <td>Ночные часы</td>
          <td>{{shiftsNights}}</td>
          <td>{{shiftsNights * 7}}</td>
          <td>{{tarifNights}}</td>
        </tr>
        <tr v-if="harm">
          <td>Вредность</td>
          <td></td>
          <td></td>
          <td>{{harm}}</td>
        </tr>
        <tr>
          <td>Премия</td>
          <td></td>
          <td></td>
          <td>{{award}}</td>
        </tr>
        <tr v-if="overtime">
          <td>Доплата за подработки</td>
          <td>{{overtime}}</td>
          <td>{{overtime*11}}</td>
          <td>{{overtimeTarif}}</td>
        </tr>
        <tr v-if="holidays">
          <td>Доплата за праздничные дни</td>
          <td></td>
          <td>{{holidays}}</td>
          <td>{{holidaysTarif}}</td>
        </tr>
        <tr>
          <td>Районный коэффициент</td>
          <td></td>
          <td></td>
          <td>{{coef}}</td>
        </tr>
        <tr>
          <td>
            <b>Итого начислено</b>
          </td>
          <td></td>
          <td></td>
          <td>{{totalIncome}}</td>
        </tr>
        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
      </tbody>
      <thead>
        <tr>
          <th>Расход</th>
          <th></th>
          <th></th>
          <th>Сумма</th>
        </tr>
        <tr>
          <td>НДФЛ</td>
          <td></td>
          <td></td>
          <td>{{ndfl}}</td>
        </tr>
      </thead>
    </table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "final",
  data: () => ({
    salary: null
  }),
  computed: {
    ...mapGetters(["getRate", "getShifts", "getOvertime", "getOther"]),
    shiftsTotal() {
      return +this.getShifts.total;
    },
    tarif() {
      return (+this.getRate * this.shiftsTotal * 11).toFixed(2);
    },
    shiftsNights() {
      return this.getShifts.nights;
    },
    tarifNights() {
      return (+this.getRate * this.shiftsNights * 7 * 0.4).toFixed(2);
    },

    harm() {
      return this.getOther.harm ? (this.tarif * 0.08).toFixed(2) : 0;
    },
    award() {
      return (this.tarif * 0.3).toFixed(2);
    },

    overtime() {
      return +this.getOvertime.overtime;
    },
    overtimeTarif() {
      return (+this.getRate * this.overtime * 11).toFixed(2);
    },

    holidays() {
      return +this.getOvertime.holidays;
    },
    holidaysTarif() {
      return (+this.getRate * this.holidays).toFixed(2);
    },

    income() {
      return (
        +this.tarif +
        +this.tarifNights +
        +this.award +
        +this.harm +
        +this.overtimeTarif +
        +this.holidaysTarif
      );
    },
    coef() {
      return (this.income * 0.15).toFixed(2);
    },

    totalIncome() {
      return (this.income + +this.coef).toFixed(2);
    },

    ndfl() {
      let food = this.getOther.eat ? (+this.getOther.eat * 75) : 0
      return ((+this.totalIncome + food) * 0.13).toFixed(2)
    },

    total() {
      return (this.totalIncome - this.ndfl).toFixed(2)
    }
    //   let income = {

    //   }
    //    let rate = this.$store.getters.getRate
    //    let shifts = this.$store.getters.getShifts
    //    let overtime = this.$store.getters.getOvertime
    //    let other = this.$store.getters.getOther

    //    let tarif = rate*11*shifts.total
    //    let hightTarif = +rate*7*0.4*(+shifts.nights)
    //    let harm = other.harm ? tarif * 0.08 : 0
    //    let award = tarif*0.3
    //  //   let holidays = overtim
    //    let coef = (tarif + hightTarif + harm + award)*0.15
    //    let total = tarif + hightTarif + harm + award + coef
    //    console.log(coef);

    //     let ndfl = total*0.13
    //    console.log(ndfl);
    //     this.salary = (total - ndfl).toFixed(2)
  }
};
</script>

<style lang='scss' scoped>
label {
  display: block;
  margin-bottom: 1rem;
}
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>